import React, {Component}from 'react';
import MaterialTable from "material-table";

import SenaServicios from '../../../services/SenaServicios';


function btnDownload(url, handle){
    return(
        <a className="text-center" onClick={handle} href={url} download>
            <div className="icon-display " id="icon-display-down-arrow-n">
                <span className="govco-icon govco-icon-down-arrow-n"></span>
            </div>
            <div className="icon-name d-none d-sm-block">
                <p>Descargar</p>
            </div>
        </a>
    )
}


class TableDetailConsult extends Component {
    constructor(props) {
        super(props);
        this.certificados = new SenaServicios();
        this.state = {
            mensaje: false,
            resultados: [],
            boxAll: false,
            linksDescargas: []
        };

        this.onSortChange = this.onSortChange.bind(this);
        this.handleDescargaSena = this.handleDescargaSena.bind(this);
        this.handleCerrarMensaje = this.handleCerrarMensaje.bind(this);

    }


    componentDidMount() {
        let resultados = this.certificados.getCertificados(this.props.tipoDocumento, this.props.numeroDocumento);
        let datos = [];
        let self = this;

        resultados.then((response) => response.json())
        .then(function(data) {
            console.log(data);
            for (let x=0; x < data.length; x++) {
                let dato = {
                    tipo: "Certificado",
                    nombre: data[x].NOMBRE_APRENDIZ,
                    programa: data[x].PROGRAMA,
                    fecha: data[x].FECHA_CERTIFICACION.split(" ")[0],
                    descargas: (btnDownload(data[x].URLCERT, self.handleDescargaSena))
                }
                datos.push(dato);
            }
            self.setState({resultados: datos});
        });
      }

    links(id, url, e){
        let list = this.state.linksDescargas;
        if(e.target.checked){
            list.push({id: id, url: url});
        }
        else{
            list = list.filter(item => item.id !== id)
        }
        return list;
    }
    handleChangeCheck = (id, url, event) => {
        this.setState({ 
            boxAll: event.target.checked,
            linksDescargas: this.links(id, url, event)
         }, () => {
          console.log(this.state.linksDescargas);
        });
    };
    onSortChange(sortName, sortOrder) {
        this.setState({
            sortName,
            sortOrder
        });
    }

    handleDescargaSena(event) {
        this.setState({mensaje: true});
    }

    handleCerrarMensaje(event) {
        this.setState({mensaje: false});
    }

    handleDescargaMasiva = () =>{
        let linkDescargas = this.state.linksDescargas;
        for (var i=0; i<linkDescargas.length;i++)
        {
            let link = linkDescargas[i].url;
            setTimeout( () =>{
               window.open(link, "_blank");
            }, 500);
        }
    }

    render(){
        let mensaje = null;

        if (this.state.mensaje) {
            mensaje = (<div className="p-3">
                <div className="toast toast-check">
                    <button className="toast-close-button" onClick={this.handleCerrarMensaje}>
                        <i className="material-icons icon">clear</i>
                    </button>
                    <div className="d-flex">
                        <div className="pr-2  govco-icon govco-icon-bell-sound color-green"></div>
                            <div className="toast-title ">
                                <span className="govco-icon govco-icon-check-cn color-green"></span>
                                <h3>Descarga exitosa</h3> 
                            </div>
                        </div>
                    </div>
                </div>
            );
        }
            return (
            <>
                <div className="card govco-card rounded rounded-lg">
                    <div className="wrapper-title-subdownloads">
                        <h6>Certificados y constancias académicas - SENA</h6>
                        <h3>Seleccionar los certificados a descargar</h3>
                    </div>

                        <MaterialTable
                            options={{
                                search:false,
                                paging: false,
                                showTitle: false,
                                headerStyle: {
                                    backgroundColor: '#e6effd',
                                },

                            }}
                            localization={{
                                body:{
                                    emptyDataSourceMessage : "No se encuentran registros para mostrar"
                                }
                            }}
                            columns={[
                                { title: "Tipo", field: "tipo" },
                                { title: "Nombre aprendiz", field: "nombre" },
                                { title: "Programa", field: "programa" },
                                { title: "Fecha", field: "fecha"},
                                { title: "Seleccionar", field: "descargas" },  
                            ]}
                            data={this.state.resultados}
                        />

                    </div>
                    {mensaje}

          </>
        );
    }
}

export default TableDetailConsult;