import React, {Component}from 'react';
import TableDetailConsult from './TableDetailConsult'
import 'react-tabs/style/react-tabs.css';

import ContraloriaServicios from '../../../services/ContraloriaServicios';


class ResultConsult extends Component {
    constructor(props) {
        super(props);
        this.certificado = new ContraloriaServicios();
        this.state = {
            mensaje: false, 
            sena: false,
        };

        this.handleDescargaContraloria = this.handleDescargaContraloria.bind(this);
        this.handleCerrarMensaje = this.handleCerrarMensaje.bind(this);
        this.handleDescargaSena = this.handleDescargaSena.bind(this);
        this.handleCerrar = this.handleCerrar.bind(this);
    }

    componentWillReceiveProps() {
        this.setState({mensaje: false});
        this.setState({sena: false});
    }

    handleDescargaSena(event){
        this.setState({sena: true})
    }

    handleCerrar(event) {
        this.setState({mensaje: false});
        this.setState({sena: false});
    }

    handleDescargaContraloria(event) {
        let blob = this.certificado.getCertificado(this.props.tipoDocumento, this.props.numeroDocumento);
        blob.then(response => {
            response.blob().then(blob => {
                if (window.navigator.msSaveBlob) {                // ie block
                    console.log("found msSaveBlob function - is't IE")
                    //var blobObject = new Blob(blob, {type: ' type: "application/pdf; charset=utf-8"'});
                    window.navigator.msSaveOrOpenBlob(blob, "certificado.pdf");
                } else {
                    let url = window.URL.createObjectURL(blob);
                    let a = document.createElement('a');
                    a.href = url;
                    a.download = 'certificado.pdf';
                    a.click();
                }
            });
        });

        this.setState({mensaje: true});
    }

    handleCerrarMensaje(event) {
        this.setState({mensaje: false});
    }

    render(){
        let mensaje = null;
        let tablaDescargasMostrar = null;

        if (this.state.mensaje) {
            mensaje = (<div className="p-3">
                <div className="toast toast-check">
                    <button className="toast-close-button" onClick={this.handleCerrarMensaje}><i className="material-icons icon">clear</i></button>
                    <div className="d-flex">
                        <div className="pr-2  govco-icon govco-icon-bell-sound color-green"></div>
                            <div className="toast-title ">
                                <span className="govco-icon govco-icon-check-cn color-green"></span>
                                <h3>Descarga exitosa</h3>
                            </div>
                        </div>
                    </div>
                </div>
            );
        }
        if(this.state.sena)
        {
            tablaDescargasMostrar = (
                <div>
                    <TableDetailConsult tipoDocumento={this.props.tipoDocumento} numeroDocumento={this.props.numeroDocumento} />
                </div>
            );
        }
        return (
            <>
            <div id="ResultConsult" className="pt-5">
                <div className="card govco-card w-100 ">	

                    <div className="card-body py-0">
 
                            
                            <h3 className="m-0">Selecciona la entidad</h3>
                              
                            {mensaje}

                            <div className="container p-2">
                                <div className="item-list">
                                    <div className="row m-0">
                                        <div className="col-2 my-auto text-center">
                                            <img src={ require('../../../assets/images/Contraloria.png') }  className="img-fluid" alt="Contraloria General de la República"/>
                                        </div>
                                        <div className="col-8 my-auto">
                                            <h3>Certificado de Antecendentes Fiscales</h3>
                                            <p>Contraloría General de la República</p>
                                        </div>
                                        <div className="col-2 my-auto">
                                            <button className="btn-descarga" onClick={this.handleDescargaContraloria}>
                                                <div className="icon-display " id="icon-display-down-arrow-n">
                                                    <span className="govco-icon govco-icon-down-arrow-n"></span>
                                                </div>
                                                <div className="icon-name d-none d-sm-block">
                                                    <p>Descargar</p>
                                                </div>
                                            </button>
                                        </div>
                                    </div>
                                </div>
                                <div className="item-list"  onClick={this.handleDescargaSena}>
                                    <div className="row m-0">
                                        <div className="col-2 my-auto text-center">
                                            <img src={ require('../../../assets/images/Sena.png') }  className="img-fluid" alt="Contraloria General de la República"/>
                                        </div>
                                        <div className="col-8 my-auto">
                                            <h3>Certificados y Constancias académicas </h3>
                                            <p>Servicio Nacional de Aprendizaje - SENA</p>
                                        </div>
                                        <div className="col-2 my-auto">
                                            <button className="text-center btn-descarga" onClick={this.handleDescargaSena}>
                                                <div className="icon-display " id="icon-display-down-arrow-n">
                                                    <span className="govco-icon govco-icon-down-arrow-n"></span>
                                                </div>
                                                <div className="icon-name d-none d-sm-block">
                                                    <p>Descargar</p>
                                                </div>
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </div>

                    </div>	
                </div>
            </div>
            <div id="ResultConsult" className="pt-5">
                <div className="tabs-govco">	
                    {tablaDescargasMostrar}
                </div>
            </div>
            </>
        );
    }
}

export default ResultConsult;