class ContraloriaServicios {
    getCertificado(tipoDocumento, numeroDocumento) {
      let url = "https://api-interno.www.gov.co/api/tramites-y-servicios/AntecedentesFiscales/generarCertificado";
      let data = {
          tipoDocumento: tipoDocumento,
          documento: numeroDocumento
      };
  
      return fetch(url, {
          method: 'POST',
          body: JSON.stringify(data),
          headers:{
              'Content-Type': 'application/json'
          }
      });
    }
}
  
export default ContraloriaServicios;