import React, {Component}from 'react';
import ResultConsult from './ResultConsult';
import Select from 'react-select';
import ReCAPTCHA from "react-google-recaptcha";
import { serviciosOnline } from '../../../services/ServiciosOnline';


class MainContainer extends Component {

    constructor(props) {
        super(props);
        this.state = {
            tipoDocumento: { value: 'CC', label: 'Cédula de Ciudadanía' },
            numeroDocumento: '',
            resultados: false,
            btnConsultar: (<button className="btn btn-consultar btn-round btn-high">CONSULTAR</button>),
            error: false,
            errorRecaptcha: false,
            nuevaConsulta: false,
            isAccordionOpen: false,            
        };
        this.recaptchaRef = React.createRef();
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        //this.handleChangeInput = this.handleChangeInput.bind(this);
        this.handleToggle  = this.handleToggle.bind(this);
    }

    handleChangeInput(numeroDocumento) { this.setState({numeroDocumento: numeroDocumento.value}); }
    handleChange(tipoDocumento) {this.setState({tipoDocumento});}
    handleFocusColapse(){this.setState({ isAccordionOpen: false});}

    handleSubmit(event) {
        const recaptchaValue = this.recaptchaRef.current.getValue();
        if (!this.state.nuevaConsulta) {
            if (event.target.numeroDocumento.value === '' && recaptchaValue === '' ) 
            {
                this.setState({error: true});
                this.setState({errorRecaptcha: true});
                this.setState({resultados: false});
            }
            else if (event.target.numeroDocumento.value === '') {
                this.setState({error: true});
                this.setState({errorRecaptcha: false});
                this.setState({resultados: false});
            }
            else if (recaptchaValue  === '' ) 
            {
                this.setState({errorRecaptcha: true});
                this.setState({error: false});
                this.setState({resultados: false});
            }
            else {
                this.setState({numeroDocumento: event.target.numeroDocumento.value});
                this.setState({resultados: true});
                this.setState({error: false});
                this.setState({errorRecaptcha: false});
                this.setState({nuevaConsulta: true});
            }
        } else {
            this.recaptchaRef.reset();
            this.setState({nuevaConsulta: false});
            this.setState({resultados: false});
            this.setState({error: false});
            this.setState({errorRecaptcha: false});
            this.setState({numeroDocumento: ''});
            this.setState({tipoDocumento: { value: 'CC', label: 'Cédula de Ciudadanía' }});            
        }
        
        event.preventDefault();
    }
    handleToggle() {
        this.setState({ isAccordionOpen: !this.state.isAccordionOpen});
    }
    
    render(){
        const servicios = serviciosOnline;
        let resultadosMostrar = null;
        let errorMostrar = null;
        let btnConsultar = this.state.btnConsultar;


        const serviciosDescargaOnlineMostrar = servicios.map((servicio, i) => 
        {
            return (
            <a className="item-list d-block" key={i} href={servicio.url}>
                <div className="row m-0 py-0">
                    <div className="col-12 my-auto p-0 text-left">
                        <h4 className="m-0">
                            {servicio.tramite}
                        </h4>
                        <p>{servicio.entidad}</p>
                    </div>
                </div>
            </a>
            )
        });

        //const { tipoDocumento } = this.state;
        const tiposDocumento = [
            { value: 'CC', label: 'Cédula de Ciudadanía' },
            { value: 'CE', label: 'Cédula de Extranjería' },
            { value: 'TI', label: 'Tarjeta de identidad' },
            { value: 'PA', label: 'Pasaporte' }
        ];
        
        if (this.state.resultados) {
            resultadosMostrar = <ResultConsult tipoDocumento={this.state.tipoDocumento.value} numeroDocumento={this.state.numeroDocumento} />
            btnConsultar = resultadosMostrar != null?
                (<button className="btn btn-consultar btn-round btn-middle">NUEVA CONSULTA</button>):
                btnConsultar;
        } else {
            resultadosMostrar = '';
        }

        if (this.state.error || this.state.errorRecaptcha) {
            errorMostrar = <p className="input-validation text-center">Los campos obligatorios deben estar diligenciados</p>;
        }

        return (
            <>
                <nav className="breadcrumb">
                    <ol className="breadcrumb">
                        <li className="breadcrumb-item">
                            <a href="/">
                                <span className="breadcrumb-home">Inicio</span>
                            </a>
                        </li>
                        <li className='breadcrumb-item active'>
                            <div className="breadcrumb">
                                <span className="govco-icon govco-icon-shortr-arrow mx-1"></span>
                                <span className="breadcrumb-text">Consulta de Certificados</span>
                            </div>
                        </li>
                    </ol>
                </nav>
                <main className="container-informacion">
                    <div className="gov-co-informacion">
                        <h2 >Consulta de certificados</h2>
                    </div>

                    <div className="gov-co-informacion-body">
                        <div className="row row-flex govco-entidades-container">
                            <div className="col-md-12 p-0">
                                <div className="row justify-content-center w-100">
                                    <div className="card govco-card w-100 mb-5">
                                        <div className="accordion accordion-govco-simple w-100" id="EjemploAccordionSimple1">
                                            <div className="card">
                                                <div className="card-header row no-gutters" id="headingSimple-1">
                                                    <button className="btn-link row no-gutters collapsed border-0" type="button" onClick={this.handleToggle.bind(this)} data-toggle="collapse" data-target="#collapseSimple" aria-expanded="true" aria-controls="collapseSimple">
                                                        <div className="col-11">
                                                            <h3 className="m-0">Certificados disponibles en descarga online</h3>
                                                        </div>
                                                        <div className="col-1">
                                                            <div className="btn-icon-close">
                                                                <span className="label-icon">cerrar</span>
                                                                <span className={"govco-icon govco-icon-shortr-arrow" + (this.state.isAccordionOpen ? ' up' : ' down')}></span> 
                                                            </div>
                                                        </div>
                                                    </button>
                                                </div>
                                                <div id="collapseSimple" className={"collapse" + (this.state.isAccordionOpen ? ' in' : '')} aria-labelledby="headingSimple-1" data-parent="#EjemploAccordionSimple1">
                                                    <div className="card-body p">
                                                        {serviciosDescargaOnlineMostrar}
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="card govco-card w-100" id="descarga-directa">
                                        <form className="mb-0" onSubmit={this.handleSubmit}>
                                            <div className="card-body py-0">
                                                <div className="row">
                                                    <div className="col-lg-12 col-md-12 col-sm-12 pl-0">
                                                        <h3>Certificados disponibles en descarga directa</h3>
                                                        <p className="title-section">Ingresa los siguientes datos para verlos:</p>
                                                    </div>
                                                </div>
                                                <div className="row" >
                                                    <div className="col-lg-6 col-md-12 col-sm-12 pl-lg-0">
                                                        <div className="form-group">
                                                            <label className="form-group-govco">Tipo de documento *</label>
                                                            <div className="form-group select-govco">
                                                            <Select
                                                                name="tipoDocumento"
                                                                placeholder="Seleccionar"
                                                                className="selectpicker"
                                                                classNamePrefix="dropdown"
                                                                value={this.state.tipoDocumento}
                                                                onChange={this.handleChange}
                                                                onFocus={ this.handleFocusColapse.bind(this) } 
                                                                options={tiposDocumento}
                                                                theme={theme => ({
                                                                    ...theme,
                                                                    borderRadius: 0,
                                                                    colors: {
                                                                        ...theme.colors,
                                                                        primary: '#0B457F',
                                                                    },
                                                                })}
                                                            />
                                                            </div>
                                                        </div>    
                                                    </div>
                                                    <div className="col-lg-6 col-md-12 col-sm-12">
                                                        <div className="form-group">
                                                            <label className={this.state.error ? 'form-group-govco input-label-error' : 'form-group-govco'}>Número de documento *</label>
                                                            <input value={this.state.numeroDocumento} onFocus={ this.handleFocusColapse.bind(this) }  onChange={this.handleChangeInput.bind(this)} className={this.state.error ? 'form-control input-govco input-document input-error' : 'form-control input-govco input-document'} type="text" maxLength="16" pattern="^[0-9]*$" name="numeroDocumento" />
                                                        </div>                
                                                    </div>
                                                </div>
                                                <div className="row justify-content-center" >
                                                    <div className="col-md-12 mt-3 mb-2" >
                                                        <div className="row recaptcha-container justify-content-center" >
                                                            <div className={this.state.errorRecaptcha ? "form-group recaptcha-error":"form-group"}>
                                                            <ReCAPTCHA
                                                                ref={this.recaptchaRef}
                                                                sitekey="6LeCqOcUAAAAAMu9McR1zoUH0z6wDj_dekAODW5h"
                                                            />
                                                            </div>
                                                        </div>     
                                                    </div>            
                                                </div>
                                                {errorMostrar}
                                                <div className="row justify-content-center">
                                                    {btnConsultar}
                                                </div> 
                                            </div> 
                                        </form>
                                    </div>

                                    <div className="rounded rounded-lg w-100">
                                        {resultadosMostrar}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </main>
            </>

        );
    }
}

export default MainContainer;