class SenaServicios {
    getCertificados(tipoDocumento, numeroDocumento) {
        let url = "https://api-interno.www.gov.co/api/tramites-y-servicios/sena/CertificadoConstancia/consultar";

        let data = {
            tipoDocumento: tipoDocumento === "PA" ? "PS" : tipoDocumento,
            documento: numeroDocumento
        };
    
        return fetch(url, {
            method: 'POST',
            body: JSON.stringify(data),
            headers:{
                'Content-Type': 'application/json'
            }
        });
    }
}
  
export default SenaServicios;